@import "../../_variables";

.homeWrapper {
  display: flex;
  flex: 100%;
  min-height: 100vh;
  background-color: $bgPrimary;
  color: $greySubheading;
  .socialLinksAndLogo {
    // flex:100%;
    width: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    background-color: $bgPrimary;
    min-height: 100vh;
    .profileLogo {
      color: $greySpecialText;
      display: flex;
      justify-content: center;
      img {
        width: 100%;
        margin-top: 20%;
      }
    }
    .socialLinks {
      display: flex;
      flex-direction: column;
      gap: 50px;
      font-size: 1.5rem;
      padding-bottom: 50%;
      a {
        text-decoration: none;
        color: $greySpecialText;
      }
      .linkedin {
        cursor: pointer;
        transition: all 0.2s ease;
        &:hover {
          color: $linkedin;
        }
      }
      .instagram {
        cursor: pointer;
        transition: all 0.2s ease;
        &:hover {
          color: $instagram;
        }
      }
      .github {
        cursor: pointer;
        transition: all 0.2s ease;
        &:hover {
          color: $github;
        }
      }
      .facebook {
        cursor: pointer;
        transition: all 0.2s ease;
        &:hover {
          color: $facebook;
        }
      }
    }
  }

  .nameAndDesc {
    flex: 48%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 10%;
    // .descTop {
    //   font-family: $fontFamily2;
    //   font-size: 4rem;
    // }
    .desc {
      font-family: $fontFamily1;
      font-size: 7rem;
      line-height: 125%;
      letter-spacing: 5px;
      color: $greySpecialText;
      .name {
        color: $red;
        text-shadow: 5px 3px $neonGreen;
      }
    }
    .descBottom {
      font-family: $fontFamily4;
      font-size: 1.2rem;
      letter-spacing: 5px;
    }
    .setOfTags {
      display: flex;
      flex-direction: column;
      .paraOpeningTag,
      .paraClosingTag,
      .headingOpeningTag,
      .headingClosingTag {
        margin-left: 3%;
      }
      .tags {
        font-style: italic;
        color: $greyHeading;
        font-family: $fontFamily3;
        letter-spacing: 2px;
        margin-top: 2%;
        font-size: 1.3rem;
      }
    }
  }

  .profilePhotoAndScrollDown {
    flex: 48%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .profilePhoto {
      width: 100%;
      img {
        width: 100%;
        // height: 100%;
      }
    }
    .scrollDownArrow {
      background: none;
      outline: none;
      border: none;
      .arrows {
        width: 60px;
        height: 72px;
        position: absolute;
        left: 50%;
        margin-left: -30px;
        bottom: 20px;
        cursor: pointer;
      }

      .arrows path {
        stroke: $neonGreen;
        fill: transparent;
        stroke-width: 1px;
        animation: arrow 2s infinite;
        -webkit-animation: arrow 2s infinite;
      }

      @keyframes arrow {
        0% {
          opacity: 0;
        }
        40% {
          opacity: 1;
        }
        80% {
          opacity: 0;
        }
        100% {
          opacity: 0;
        }
      }

      @-webkit-keyframes arrow /*Safari and Chrome*/ {
        0% {
          opacity: 0;
        }
        40% {
          opacity: 1;
        }
        80% {
          opacity: 0;
        }
        100% {
          opacity: 0;
        }
      }

      .arrows path.a1 {
        animation-delay: -1s;
        -webkit-animation-delay: -1s; /* Safari 和 Chrome */
      }

      .arrows path.a2 {
        animation-delay: -0.5s;
        -webkit-animation-delay: -0.5s; /* Safari 和 Chrome */
      }

      .arrows path.a3 {
        animation-delay: 0s;
        -webkit-animation-delay: 0s; /* Safari 和 Chrome */
      }
    }
  }

  .verticalNav {
    flex: 4%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    .about,
    .projects,
    .experiences,
    .contact {
      transform: rotate(90deg);
      font-family: $fontFamily4;
      font-size: 1rem;
      cursor: pointer;

      // margin: auto;
      a {
        letter-spacing: 2px;
        cursor: pointer;
      }
    }
  }
}
