@import "../../_variables";

.contactWrapper {
  min-height: 100vh;
  //   background-color: $bgPrimary;
  background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.7)),
    url("../../../public//assets/getInTouchBackground.png");
  background-position: center;
  .contactHeader {
    flex: 48%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 10%;
    padding-top: 3%;
    .desc {
      font-family: $fontFamily1;
      font-size: 5rem;
      line-height: 125%;
      letter-spacing: 5px;
      color: $greySpecialText;
      .name {
        color: $red;
      }
    }
    .formContainer {
      font-family: $fontFamily4;
      font-size: 1.2rem;
      letter-spacing: 5px;

      .contactForm {
        display: flex;
        flex-direction: column;
        gap: 10px;
        width: 50%;
        margin-top: 2%;
        margin-bottom: 2%;
        .nameAndContact {
          width: 100%;
          display: flex;
          gap: 10px;
          input {
            color: $greySubheading;
            width: 100%;
            padding: 2.5% 5%;
            background-color: $bgSubSecondary;
            border: none;
            border-radius: 5px;
            font-size: 1.2rem;
          }
        }
        .emailInput,
        .messageTextArea {
          color: $greySubheading;
          padding: 2.5% 5%;
          background-color: $bgSubSecondary;
          border: none;
          border-radius: 5px;
          font-size: 1.2rem;
        }
        .submitButton {
          width: 25%;
          background: none;
          outline: none;
          border: 1px solid $neonGreen;
          color: $neonGreen;
          font-size: 1.3rem;
          padding: 2% 4%;
          border-radius: 5px;
          cursor: pointer;
          transition: all 0.3s ease;
          margin-top: 3%;
          &:hover {
            background-color: $neonGreen;
            color: $bgPrimary;
          }
        }
      }
    }
    .setOfTags {
      display: flex;
      flex-direction: column;
      .paraOpeningTag,
      .paraClosingTag,
      .headingOpeningTag,
      .headingClosingTag {
        margin-left: 2%;
      }
      .tags {
        font-style: italic;
        color: $greyHeading;
        font-family: $fontFamily3;
        letter-spacing: 2px;
        font-size: 1.3rem;
      }
    }
  }
}
