.container {
  position: relative;
  width: 100%;
//   max-width: 400px;
  .image {
    width: 100%;
    height: auto;
  }
  .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: 0.3s ease;
    background-color: rgba($color: #000000, $alpha: 0.6);
    .icon {
      color: white;
      font-size: 2rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      text-align: center;
      text-decoration: none;
    }
  }
  &:hover {
    .overlay {
      opacity: 1;
    }
  }
}
