@import "../../_variables";

.overlayDisplayWrapper {
  width: 100px;
  height: 100px;
  border-radius: 100px;
  background-color: $neonGreen;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  cursor: pointer;
  button {
    background: none;
    outline: none;
    border: none;
    font-family: $fontFamily4;
    font-weight: 600;
    letter-spacing: 2px;
    cursor: pointer;
  }
  &:hover {
    transform: scale(1.2);
  }
}
