@import "../../_variables";

.experienceWrapper {
  min-height: 100vh;
  background-color: $bgSecondary;
  padding-bottom: 3%;
  .experiencesHeading {
    flex: 50%;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    margin-left: 10%;
    padding-top: 3%;
    .desc {
      font-family: $fontFamily1;
      font-size: 5rem;
      line-height: 125%;
      letter-spacing: 5px;
      color: $greySpecialText;
      .meMyselfHeading {
        color: $red;
        // text-shadow: 5px 3px $neonGreen;
      }
    }
    .descBottom {
      font-family: $fontFamily4;
      color: $greySubheading;
      font-size: 1.2rem;
      letter-spacing: 5px;
    }
    .setOfTags {
      display: flex;
      flex-direction: column;
      .paraOpeningTag,
      .paraClosingTag,
      .headingOpeningTag,
      .headingClosingTag {
        margin-left: 2%;
      }
      .tags {
        font-style: italic;
        color: $greyHeading;
        font-family: $fontFamily3;
        letter-spacing: 2px;
        // margin-top: 1%;
        // margin-bottom: 1%;
        font-size: 1.3rem;
      }
    }
  }
  .timelineWrapper {
    border-width: 1px;
margin-top: 3%;
    .vertical-timeline-element-title {
      color: $greySpecialText;
    }
    .vertical-timeline-element-subtitle {
      color: $neonGreen;
      font-style: italic;
    }
  }
}
