@import "../../_variables";

.projectsWrapper {
  min-height: 100vh;
  background: $bgSubSecondary;
  .myProjectHeading {
    flex: 50%;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    margin-left: 10%;
    padding-top: 3%;
    .desc {
      font-family: $fontFamily1;
      font-size: 5rem;
      line-height: 125%;
      letter-spacing: 5px;
      color: $greySpecialText;
      .meMyselfHeading {
        color: $red;
        // text-shadow: 5px 3px $neonGreen;
      }
    }
    .descBottom {
      font-family: $fontFamily4;
      color: $greySubheading;
      font-size: 1.2rem;
      letter-spacing: 5px;
    }
    .setOfTags {
      display: flex;
      flex-direction: column;
      .paraOpeningTag,
      .paraClosingTag,
      .headingOpeningTag,
      .headingClosingTag {
        margin-left: 2%;
      }
      .tags {
        font-style: italic;
        color: $greyHeading;
        font-family: $fontFamily3;
        letter-spacing: 2px;
        // margin-top: 1%;
        // margin-bottom: 1%;
        font-size: 1.3rem;
      }
    }
  }
  .myProjectsGallery {
    display: flex;
    flex-wrap: wrap;
    margin: 3% auto auto;
    justify-content: center;
    align-items: center;
    margin-left: 5%;
    // padding: 2% 5% 0;
    .flexItem {
      flex: 20%;
      // width: ;
    }
  }
}
